.lamp {
    height: 100%;
    background-color: #090a0f;
    background: radial-gradient(
      ellipse at bottom,
      rgba(27, 39, 53, 1),
      rgba(9, 10, 15, 1)
    );
    overflow: hidden;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  
    height: 100vh;
    height: 100vh;
    width: 100vw;
    min-width: 100vw;
    z-index: -1;
  }
  
  .lava {
    filter: url('#goo');
    position: absolute;
    height: 100%;
    width: 100%;
    top: 0;
    left: 0;
  }
  
  .blob {
    border-radius: 50%;
    background: #661bac;
    position: absolute;
  }
  
  .blobTop {
    position: fixed;
    background: #661bac;
    border-radius: 50%;
    width: 100%;
    height: 4%;
    top: -3%;
    left: 0;
  }
  .blobBottom {
    position: fixed;
    background: #661bac;
    border-radius: 50%;
    width: 100%;
    height: 4%;
    bottom: -3%;
    left: 0;
  }
  .blob:nth-child(1) {
    width: 200px;
    height: 200px;
    left: 35%;
    bottom: -15%;
  
    animation: wobble 4s ease-in-out alternate infinite,
      blob-one ease-in-out 13s infinite;
  }
  .blob:nth-child(2) {
    width: 240px;
    height: 240px;
    right: 24%;
    bottom: -65%;
  
    animation: wobble 5s ease-in-out alternate infinite,
      blob-two ease-in-out 22s infinite;
  }
  .blob:nth-child(3) {
    width: 150px;
    height: 150px;
    bottom: -15%;
    left: 50%;
  
    animation: wobble 6s ease-in-out alternate infinite,
      blob-three ease-in-out 16s infinite;
  }
  .blob:nth-child(4) {
    width: 5px;
    height: 5px;
    bottom: -19%;
    left: 30%;
  
    animation: wobble 7s ease-in-out alternate infinite,
      blob-four ease-in-out 12s infinite;
  }
  
  .blob:nth-child(5) {
    width: 55px;
    height: 55px;
    bottom: -25%;
    left: 34%;
  
    animation: wobble 9s ease-in-out alternate infinite,
      blob-five ease-in-out 32s infinite;
  }
  .blob:nth-child(6) {
    width: 35px;
    height: 35px;
    bottom: -25%;
    right: 34%;
  
    animation: wobble 10s ease-in-out alternate infinite,
      blob-six ease-in-out 12s infinite;
  }
  .blob:nth-child(7) {
    width: 135px;
    height: 45px;
    bottom: -85%;
    right: 70%;
  
    animation: wobble 11s ease-in-out alternate infinite,
      blob-seven ease-in-out 32s infinite;
  }
  @keyframes blob-one {
    0%,
    100% {
      transform: translatey(0);
    }
    50% {
      transform: translatey(-700%);
    }
  }
  
  @keyframes blob-two {
    0%,
    100% {
      transform: translatey(0);
    }
    50% {
      transform: translatey(-420%);
    }
  }
  
  @keyframes blob-three {
    0%,
    100% {
      transform: translatey(0);
    }
    50% {
      transform: translatey(-305%);
    }
  }
  @keyframes blob-four {
    0%,
    100% {
      transform: translatey(0);
    }
    50% {
      transform: translatey(-605%);
    }
  }
  @keyframes blob-five {
    0%,
    100% {
      transform: translatey(0);
    }
    50% {
      transform: translatey(-700%);
    }
  }
  @keyframes blob-six {
    0%,
    100% {
      transform: translatey(0);
    }
    50% {
      transform: translatey(-700%);
    }
  }
  @keyframes blob-seven {
    0%,
    100% {
      transform: translatey(0);
    }
    50% {
      transform: translatey(-300%);
    }
  }
  
  @keyframes wobble {
    50% {
      border-radius: 42% 58% 50% 30% / 45% 45% 55% 55%;
    }
    100% {
      border-radius: 38% 12% 55% 36% / 50% 40% 50% 60%;
    }
  }
  